<template>
  <section id="projects" class="py-5 my-5">

    <div class="container">
      <h2 class="text-center py-5 mb-5 display-5 font-weight-bold">My Projects</h2>
      <div class="row">
        <!-- Display first 3 projects -->
        <div class="col-md-4" v-for="project in visibleProjects" :key="project.id">
          <div class="card shadow-lg border-0 rounded-lg">
            <img :src="require('@/assets/' + project.image)" class="card-img-top rounded-top" alt="Project Image" />
            <div class="card-body">
              <h5 class="card-title">{{ project.title }}</h5>
              <p class="card-text text-muted mb-3">{{ project.description }}</p>
              <div class="d-flex justify-content-between align-items-center">
                <a :href="project.link" class="btn">
                  <i class="bi bi-arrow-right-circle"></i> View Project
                </a>
                <span class="text-muted"><i class="bi bi-tags"></i> Web Development</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <!-- Transition wrapper for hidden projects -->
      <transition name="fade" @before-enter="beforeEnter" @enter="enter" @leave="leave">
        <div class="row mt-4" v-show="showMore">
          <div class="col-md-4" v-for="project in hiddenProjects" :key="project.id">
            <div class="card shadow-lg border-0 rounded-lg">
              <img :src="require('@/assets/' + project.image)" class="card-img-top rounded-top" alt="Project Image" />
              <div class="card-body">
                <h5 class="card-title">{{ project.title }}</h5>
                <p class="card-text text-muted mb-3">{{ project.description }}</p>
                <div class="d-flex justify-content-between align-items-center">
                  <a :href="project.link" class="btn">
                    <i class="bi bi-arrow-right-circle"></i> View Project
                  </a>
                  <span class="text-muted"><i class="bi bi-tags"></i> Web Development</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </transition>

      <!-- Toggle Button -->
      <div class="text-center mt-4">
        <button class="btn" @click="toggleProjects">
          {{ showMore ? 'Show Less' : 'See More' }}
        </button>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      showMore: false,
      projects: [
        {
          id: 1,
          title: 'Movie Main',
          description: 'A movie website providing users with film details, including trailers, reviews, and ratings.',
          image: 'movie-project.png',
          link: 'https://example.com/movie-main',
        },
        {
          id: 2,
          title: 'Personal Blog Website',
          description: 'A personal blog where I share thoughts on tech, web development, and life.',
          image: 'blog-project.png',
          link: 'https://example.com/personal-blog',
        },
        {
          id: 3,
          title: 'Robuxst Website',
          description: 'A secure platform for buying Robux and game passes on Roblox.',
          image: 'robuxst-project.png',
          link: 'https://example.com/robuxst',
        },
        // Hidden Projects
        {
          id: 4,
          title: 'Project Deadline',
          description: 'offer a simple, fast, and reliable way to enhance your Roblox experience with instant transactions and great deals.',
          image: 'project1.jpg',
          link: '#',
        },
        {
          id: 5,
          title: 'Portfolio Website',
          description: 'Welcome to my portfolio, where I showcase my web development projects and passion for creating innovative, user-friendly digital solutions.',
          image: 'project2.png',
          link: '#',
        },
        {
          id: 6,
          title: 'To Do List',
          description: 'Todolist is a simple and efficient web app to help you organize and manage your tasks with ease.',
          image: 'todolist-project.png',
          link: '#',
        },
      ],
    };
  },
  computed: {
    visibleProjects() {
      return this.projects.slice(0, 3);  // Show the first 3 projects
    },
    hiddenProjects() {
      return this.projects.slice(3);  // Show the remaining hidden projects
    },
  },
  methods: {
    toggleProjects() {
      this.showMore = !this.showMore;
    },
    beforeEnter(el) {
      el.style.opacity = 0;
    },
    enter(el, done) {
      el.offsetHeight; // trigger reflow
      el.style.transition = 'opacity 0.5s ease';
      el.style.opacity = 1;
      done();
    },
    leave(el, done) {
      el.style.transition = 'opacity 0.5s ease';
      el.style.opacity = 0;
      done();
    },
  },
};
</script>

<style scoped>
/* Add custom styles for the project section */
#projects h2 {
  font-weight: 700;
  color: #1f6674;
  text-transform: uppercase;
  letter-spacing: 2px;

}

.card {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.card-img-top {
  object-fit: cover;
  height: 200px;
}

.btn {
  border: 2px solid #1f6674;
  color: #101010;
  padding: 0.8rem 1.5rem;
  border-radius: 30px;
  font-weight: 600;
}

.btn:hover {
  background-color: #1f6674;
  color: #fff;
}

.card-body {
  padding: 1.5rem;
}

.text-muted {
  font-size: 0.9rem;
}

.bi {
  margin-right: 0.5rem;
}

.card-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #1f6674;
}

@media (max-width: 768px) {
  .card-body {
    padding: 1rem;
  }
}

@media (max-width: 576px) {
  #projects h2 {
    font-size: 2rem;
  }
  .card-body {
    padding: 0.8rem;
  }
}

/* Transition styles */
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s ease;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>
