<template>
  <section class="hero d-flex align-items-center justify-content-center min-vh-100 position-relative">
    <!-- Background Blur Gradients -->
    <div class="bg-overlay bg-gradient-teal position-absolute"></div>
    <div class="bg-overlay bg-gradient-indigo position-absolute"></div>
    <div class="bg-overlay bg-gradient-sky position-absolute d-none d-lg-block"></div>
    
    <!-- Main Content -->
    <div class="container text-center text-dark">
      <div class="row justify-content-center">
        <div class="col-12 col-md-8 col-lg-6" data-aos="fade-up" data-aos-duration="1000">
          <h1 class="display-4 mb-3 fw-bold gradient-text" data-aos="fade-up" data-aos-delay="200">
            I'm Laurence Jay
          </h1>
          <div class="divider"></div>

          <p class="lead mb-4 typing-effect" data-aos="fade-up" data-aos-delay="300">
            <span>{{ text }}</span>
          </p>
          <div class="social-icons mb-4 d-flex justify-content-center gap-4">
            <a href="https://www.facebook.com/rencezzyy" target="_blank" class="text-decoration-none ">
              <i class="fab fa-facebook fa-2x icon-hover"></i> Facebook
            </a>
            <a href="https://github.com/rencezy07" target="_blank" class="text-decoration-none">
              <i class="fab fa-github fa-2x icon-hover"></i> GitHub
            </a>
          </div>
          <div class="buttons d-flex justify-content-center gap-3">
            <a href="#about" class="btn about-btn btn-lg" data-aos="fade-up" data-aos-delay="400">
              About Me
            </a>
            <a href="#projects" class="btn custom-btn btn-lg shadow-lg" data-aos="fade-up" data-aos-delay="400">
              View My Work
            </a>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import AOS from 'aos';
import 'aos/dist/aos.css';

export default {
  data() {
    return {
      text: '',
      typingSpeed: 100,
      typingIndex: 0,
      typingContent: 'A passionate Web Developer who loves building amazing websites.',
    };
  },
  mounted() {
    AOS.init();
    this.startTyping();
  },
  methods: {
    startTyping() {
      if (this.typingIndex < this.typingContent.length) {
        this.text += this.typingContent.charAt(this.typingIndex);
        this.typingIndex++;
        setTimeout(this.startTyping, this.typingSpeed);
      }
    },
  },
};
</script>

